<svelte:head>
    <link rel="stylesheet" href="https://stackpath.bootstrapcdn.com/bootswatch/4.5.0/flatly/bootstrap.min.css">
    <script src="https://cdn.paddle.com/paddle/paddle.js" crossorigin="anonymous" SameSite="Secure"></script>
<script type="text/javascript">
	Paddle.Setup({ vendor: 116844 });
</script>
</svelte:head>
<script>
    export let displayName;
    export let email;
    export let emailVerified;
    //export let photoURL;
    export let uid;
    let checkoutId;
    let isPro = false;

  import App from './App.svelte';
  import { onMount } from 'svelte';
  import { Router, Link, link, Route } from "svelte-routing";
  import {Row, Table, Label, Container, Button, Col, Card, CardBody} from 'sveltestrap';
  
  import { auth, googleProvider, twitterProvider, db, firebaseAnalytics } from './firebase';
  let user = auth.currentUser;

    function sendEmailVerify(){
    user.sendEmailVerification().then(function() {
    // Email sent.
    alert("Email Verification Sent! Please check SPAM if not found!")
    }).catch(function(error) {
    // An error happened.

    });
    }

    function checkoutClosed(data) {
    console.log(data);
    alert('Your purchase has been cancelled, we hope to see you again soon!');
    }

	function openMCheckout() {
        firebaseAnalytics.logEvent('popup_clicked_MonthlyCheckout', {
            paymentMethod: 'Monthly_Popup_opened'
        })
        Paddle.Checkout.open({ 
            product: 597953,
            email: email,
            completeDetails: true,
            closeCallback:"checkoutClosed",
            successCallback: function(data) {
                checkoutId = data.checkout.id;
                
                Paddle.Order.details(checkoutId, function(data) {
                // Order data, downloads, receipts etc... available within 'data' variable.
                console.log(data);

                //To call firebase db and add details so that we can know this is a pro user, by saving there checkout ID and email
                    db.collection('PROusers').doc(uid).set({
                        email: email,
                        checkoutId: checkoutId,
                        planType: "Monthly",
                        SubscriptionDate: Date()
                    })
                }).then(()=> {
                    setTimeout(function(){ alert("Payment Successful! Enjoy your PRO Access."); }, 2000);
                });
            }
        });
    }

    function openYCheckout() {
        firebaseAnalytics.logEvent('popup_clicked_YearlyCheckout', {
            paymentMethod: 'Yearly_Popup_opened'
        })
        Paddle.Checkout.open({ 
            product: 597966,
            completeDetails: true,
            email: email,
            successCallback: function(data) {
                checkoutId = data.checkout.id;
                
                Paddle.Order.details(checkoutId, function(data) {
                // Order data, downloads, receipts etc... available within 'data' variable.
                console.log(data);

                //To call firebase db and add details so that we can know this is a pro user, by saving there checkout ID and email
                    db.collection('PROusers').doc(uid).set({
                        email: email,
                        checkoutId: checkoutId,
                        planType: "Yearly",
                        SubscriptionDate: Date()
                    })
                }).then(()=> {
                    setTimeout(function(){ alert("Payment Successful! Enjoy your PRO Access."); }, 2000);
                });
            }
        });
    }

    function emailUserTransactions(){
        firebaseAnalytics.logEvent('Emailed_user_txn_info')
        Paddle.User.History(email, null, function(response) {					
            if(response.success) {
                // Let the user know they'll receive an email with their 
                // order history and license codes if their email was matched.
                alert(response.message)
            } else {
                // Likely a formatting error in the email, vendorID, or ProductID.
            }
            });
    }

    onMount(() => {
        
    // Now to check if a user is PRO or NOT by checking PRO users database in firebase.
    db.collection('PROusers').doc(uid).get().then(doc => {
        if (doc.exists) {
        console.log("Document data:", doc.data());
            isPro = true;
    } else {
        // doc.data() will be undefined in this case
        console.log("No such document!");
        isPro = false;
    }
    }).catch(function(error) {
        console.log("Error getting document:", error);
    });
    });

</script>
<Container fluid>
    <Row class="login text-center py-5">
        <Col sm="12" class="m-2">
            <h4>Welcome back, { displayName }!</h4>
        </Col>
        <Col sm="12" class="m-2">
            <a href="/App" use:link><Button class="w-25" color="primary">Start Designing</Button></a>
        </Col>
        <Col sm="12" class="m-2">
            <Container fluid>
				<Card body>
					<Row>
						<Col>
							<h5 class="text-info text-left">📢 Announcements:-</h5>
						</Col>
					</Row>
					<Row>
                        <Col sm="12" class="text-left">
							<h6>1. We are super happy to have you onboard. To get started, Please click on the button <span class="text-success">Start Designing</span>.</h6>
                        </Col>
                        <Col sm="12" class="text-left">
							<h6>2. Your feedback is valuable to us. You can give feedback in the App page, with the <span class="text-info">Feedback</span> button .</h6>
                        </Col>
                        <Col sm="12" class="text-left">
							<h6>3. This Web Application is Cached and thus, it works fast 🚀. You can make designs on mobile as well .</h6>
                        </Col>
                        {#if isPro}
                        <Col sm="12" class="text-left">
							<h6>4. If you need help and support, Click on the <span class="text-warning">Need Help</span> button below.</h6>
                        </Col>
                        {:else}
                        <Col sm="12" class="text-left">
							<h6>4. For the Complete Experience, we recommend you to upgrade to PRO. <span class="text-primary font-weight-bold">As a token of appreciation, Your first month will be on us.</span></h6>
                        </Col>
                        {/if}
                    </Row>
                    {#if !isPro}
                    <Row class="mt-2">
                        <Col>
                            <h6>Join Monthly for $8/Mo</h6>
                            <Button color="secondary" on:click={openMCheckout}>Upgrade to Pro<br/> (Monthly)</Button>
                        </Col>
                        <Col>
                            <h6>Join Yearly for <del>$96</del> <b>$80/Yr</b></h6>
                            <Button color="primary" on:click={openYCheckout}>Upgrade to Pro<br/> (Yearly)</Button>
                        </Col>
                    </Row>
                    {/if}
                </Card>
                </Container>
        </Col>
    </Row>

    <div class="login bg-light p-2 mt-3">
    <Table>
        <thead>
            <tr><td>
            <h5>Account Information:-</h5>
        </td>
        </tr>
        </thead>
        <tbody>
            <tr>
        <td>
            <Label for="emailID">Your Email:-</Label>
        </td>
        <td>
            <input class="w-50" type="text" value={email} disabled/>
        </td>
    </tr>
    <tr>
            <td><Label for="userName">Account Type:-</Label></td>
            <td>
                {#if isPro}<span class="text-primary">Pro 😎</span>{:else}<span class="text-primary">FREE</span>{/if}
            </td>
    </tr>
    <tr>
            <td><Label for="userName">Email Status:-</Label></td>
            <td>
                {#if emailVerified}<span class="text-primary">Verified</span>{:else}<span class="text-danger">Email not verified!<Button on:click={sendEmailVerify}>Verify Email</Button></span>{/if}
            </td>
    </tr>
    {#if isPro}
    <tr>
            <td><Label for="userName">Subscription / Transactions Info. :-</Label></td>
            <td>
                <Button color="info" on:click={emailUserTransactions}>Email Me</Button>
            </td>
    </tr>
    <tr><td>
            <Row>       
                <Col sm="12" class="m-2">
                    <a href="/Support" use:link><Button class="w-25" color="warning">Need Help?</Button></a>
                </Col>
                </Row>
                
                </td></tr>
    {/if}
    </tbody>
    </Table>
</div>
</Container>